:root {
  --app-height: 100%;
}

html {
  margin: 0 !important;
}

* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

button, label {
  cursor: pointer;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: Exo2, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #FFF;
  background-color: #012238;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #012238;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #012238;
}
body::-webkit-scrollbar-track {
  background: #DFF8F6;
  border-radius: 0;
}
body {
  scrollbar-face-color: #012238;
  scrollbar-track-color: #DFF8F6;
}
body.overflow {
  overflow: hidden;
  padding-right: var(--scroll-width);
}
body.overflow .header {
  padding-right: var(--scroll-width);
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 100%;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    max-width: 685px;
  }
}
@media (min-width: 1024px) {
  .container {
    max-width: 100%;
    padding: 0 40px;
  }
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(7px);
  padding: 40px 20px 15px;
  z-index: 10;
}
@media (min-width: 1024px) {
  .header {
    padding: 20px;
  }
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
@media (min-width: 768px) {
  .header__content {
    max-width: 645px;
    margin: 0 auto;
  }
}
@media (min-width: 1024px) {
  .header__content {
    max-width: 945px;
  }
}
@media (min-width: 1441px) {
  .header__content {
    max-width: 1712px;
  }
}
.header__content .logo-link {
  display: block;
  width: auto;
}
.header__content .logo-link .logo-image {
  display: block;
  width: auto;
  max-width: 100%;
  margin: 0;
}
@media (min-width: 1024px) {
  .header__content .logo-link .logo-image {
    width: 130px;
  }
}
@media (min-width: 1441px) {
  .header__content .logo-link .logo-image {
    width: 145px;
  }
}
.header__content .phone-link {
  display: inline-block;
  margin: 0 0 0 auto;
  color: #fff;
  font-family: Lineatura, sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  position: relative;
  top: -3px;
}
@media (min-width: 1024px) {
  .header__content .phone-link {
    font-size: 18px;
  }
}

@keyframes stars {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}
@keyframes stars_hide {
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
}
@keyframes fly {
  0%, 100% {
    transform: translate(-5px, 5px);
  }
  50% {
    transform: translate(0px, 0px);
  }
}
@keyframes fly_cloud {
  0%, 100% {
    transform: translate(-15px, -5px);
  }
  50% {
    transform: translate(0px, 0px);
  }
}
@keyframes fly_rocket {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(200vw, -20vh);
  }
}
@keyframes fly_rocket_big {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(200vw, -45vh);
  }
}
.main-slider {
  position: relative;
  width: 100%;
  height: 100vh;
}
.main-slider:before {
  content: "";
  display: block;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: radial-gradient(50% 50% at 50% 50%, #036C75 0%, #012238 100%);
  pointer-events: none;
}
.main-slider > .swiper-wrapper > .swiper-slide {
  position: relative;
}
.main-slider > .swiper-wrapper > .swiper-slide:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../images/common/ancors/stars_1.png);
  background-position: top left;
  animation: stars 2s ease 0s infinite normal forwards;
  pointer-events: none;
}
.main-slider > .swiper-wrapper > .swiper-slide:after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(../images/common/ancors/stars_2.png);
  background-position: top left;
  animation: stars_hide 2s ease infinite normal forwards;
  pointer-events: none;
}
.main-slider .section-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  position: relative;
  z-index: 1;
}

/* main slide */
.main-page {
  z-index: 10;
}
.main-page .section-content {
  padding: 76px 20px 20px;
}
.main-page__title {
  font-family: Lineatura, sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 150%;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.4);
  margin-top: 5.5vh;
  max-width: 400px;
}
@media (min-width: 768px) {
  .main-page__title {
    margin-top: 10.25vh;
  }
}
@media (min-width: 1024px) {
  .main-page__title {
    margin-top: 9.25vh;
    font-size: 50px;
    max-width: 800px;
  }
}
@media (min-width: 1441px) {
  .main-page__title {
    font-size: 60px;
    max-width: 1000px;
    margin-top: 7.25vh;
  }
}
.main-page__title span {
  color: rgb(88, 220, 227);
}
.main-page__subtitle {
  font-family: Exo2, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  margin-top: 24px;
}
@media (min-width: 768px) {
  .main-page__subtitle {
    text-transform: uppercase;
  }
  .main-page__subtitle br {
    display: none;
  }
}
@media (min-width: 1024px) {
  .main-page__subtitle {
    font-size: 16px;
    margin-top: 50px;
  }
}
.main-page__subtitle span {
  color: rgb(88, 220, 227);
}
.main-page__btn-row {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 35px;
}
@media (min-width: 768px) {
  .main-page__btn-row {
    margin-top: 45px;
  }
}
@media (min-width: 1441px) {
  .main-page__btn-row {
    margin-top: 35px;
  }
}
.main-page__btn-row .test-link {
  display: inline-block;
  background-color: #09D3C6;
  font-family: Exo2, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #fff;
  text-transform: uppercase;
  box-shadow: 0px 8px 15px rgba(9, 211, 198, 0.55);
  border-radius: 10px;
  padding: 18px 40px;
  transition: background-color 0.3s, box-shadow 0.3s;
}
@media (min-width: 1024px) {
  .main-page__btn-row .test-link {
    padding: 20px 40px;
    font-size: 16px;
  }
}
.main-page__btn-row .test-link:hover {
  background-color: #09D335;
  box-shadow: 0px 8px 15px rgba(9, 211, 53, 0.55);
}
.main-page__rocket {
  max-width: 120px;
  position: absolute;
  left: -50vw;
  animation: fly_rocket 3.5s ease-in-out 0s infinite;
  z-index: -1;
  margin-top: 8vh;
}
@media (min-width: 768px) {
  .main-page__rocket {
    max-width: 145px;
    width: 18vh;
    margin-top: 5.5vh;
  }
}
@media (min-width: 1024px) {
  .main-page__rocket {
    animation: fly_rocket 5.5s ease-in-out 0s infinite;
    margin-top: 22.5vh;
  }
}
@media (min-width: 1441px) {
  .main-page__rocket {
    margin-top: 52vh;
    max-width: 100%;
    animation: fly_rocket_big 8.5s ease-in-out 0s infinite;
  }
}
.main-page__image-wrapper {
  position: relative;
  flex: 1 1 auto;
  width: 100%;
  height: 100%;
  transition: transform 2s;
  pointer-events: none;
}
.main-page__image-wrapper[data-position="0"] {
  transform: translate(0, 0);
}
.main-page__image-wrapper[data-position="1"] {
  transform: translate(0, calc(50vh + 55%));
}
@media (min-width: 1024px) {
  .main-page__image-wrapper[data-position="1"] {
    transform: translate(0, calc(50vh + 50%));
  }
}
@media (min-width: 1441px) and (max-height: 1000px) {
  .main-page__image-wrapper[data-position="1"] {
    transform: translate(0, calc(50vh + 42%)) scale(0.85);
  }
}
.main-page__image-wrapper[data-position="2"] {
  transform: translate(0, calc(115vh + 100%));
}
@media (min-width: 1024px) {
  .main-page__image-wrapper[data-position="2"] {
    transform: translate(0, calc(132vh + 100%)) scale(0.8);
  }
}
@media (min-width: 1441px) {
  .main-page__image-wrapper[data-position="2"] {
    transform: translate(0, calc(125vh + 100%)) scale(0.7);
  }
}
.main-page__image-wrapper .kid {
  display: block;
  max-width: 216px;
  max-height: calc(100% - 30px);
  position: absolute;
  top: 17%;
  left: 0;
  right: 0;
  margin: 0 auto;
  pointer-events: none;
  animation: fly 3s ease 0s infinite normal forwards;
}
@media (min-width: 768px) {
  .main-page__image-wrapper .kid {
    max-width: 245px;
  }
}
@media (min-width: 1024px) {
  .main-page__image-wrapper .kid {
    max-width: 295px;
  }
}
@media (min-width: 1441px) {
  .main-page__image-wrapper .kid {
    max-width: 100%;
    max-height: unset;
    top: 14%;
  }
}