@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"
// critical animations
@keyframes stars
  0%, 100%
    opacity: 1
  50%
    opacity: 0
@keyframes stars_hide
  0%, 100%
    opacity: 0
  50%
    opacity: 1
@keyframes fly
  0%, 100%
    transform: translate(-5px, 5px)
  50%
    transform: translate(0px, 0px)
@keyframes fly_cloud
  0%, 100%
    transform: translate(-15px, -5px)
  50%
    transform: translate(0px, 0px)
@keyframes fly_rocket
  0%
    transform: translate(0, 0)
  100%
    transform: translate(200vw, -20vh)
@keyframes fly_rocket_big
  0%
    transform: translate(0, 0)
  100%
    transform: translate(200vw, -45vh)
// main settings
.main-slider
  position: relative
  width: 100%
  height: 100vh
  &:before
    content: ''
    display: block
    width: 100%
    height: 100vh
    position: fixed
    top: 0
    left: 0
    background: radial-gradient(50% 50% at 50% 50%, #036C75 0%, #012238 100%)
    pointer-events: none
  & > .swiper-wrapper
    & > .swiper-slide
      position: relative
      &:before
        content: ''
        display: block
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
        background-image: url(../images/common/ancors/stars_1.png)
        background-position: top left
        animation: stars 2s ease 0s infinite normal forwards
        pointer-events: none
      &:after
        content: ''
        display: block
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
        background-image: url(../images/common/ancors/stars_2.png)
        background-position: top left
        animation: stars_hide 2s ease infinite normal forwards
        pointer-events: none
  .section-content
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    height: 100vh
    position: relative
    z-index: 1
/* main slide */
.main-page
  z-index: 10
  .section-content
    padding: 76px 20px 20px
  &__title
    font-family: Lineatura, sans-serif
    font-weight: 700
    font-size: 26px
    line-height: 150%
    text-align: center
    text-transform: uppercase
    text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.4)
    margin-top: 5.5vh
    max-width: 400px
    @media (min-width: 768px)
      margin-top: 10.25vh
    @media (min-width: 1024px)
      margin-top: 9.25vh
      font-size: 50px
      max-width: 800px
    @media (min-width: 1441px)
      font-size: 60px
      max-width: 1000px
      margin-top: 7.25vh
    span
      color: $accent-color
  &__subtitle
    font-family: Exo2, sans-serif
    font-weight: 400
    font-size: 14px
    line-height: 21px
    text-align: center
    margin-top: 24px
    @media (min-width: 768px)
      text-transform: uppercase
      br
        display: none
    @media (min-width: 1024px)
      font-size: 16px
      margin-top: 50px
    span
      color: $accent-color
  &__btn-row
    display: flex
    align-items: center
    justify-content: center
    margin-top: 35px
    @media (min-width: 768px)
      margin-top: 45px
    @media (min-width: 1441px)
      margin-top: 35px
    .test-link
      display: inline-block
      background-color: $btn-accent-color
      font-family: Exo2, sans-serif
      font-weight: 400
      font-size: 14px
      line-height: 17px
      color: #fff
      text-transform: uppercase
      box-shadow: 0px 8px 15px rgba($btn-accent-color, 0.55)
      border-radius: 10px
      padding: 18px 40px
      transition: background-color .3s, box-shadow .3s
      @media (min-width: 1024px)
        padding: 20px 40px
        font-size: 16px
      &:hover
        background-color: $btn-green-color
        box-shadow: 0px 8px 15px rgba($btn-green-color, 0.55)
  &__rocket
    max-width: 120px
    position: absolute
    left: -50vw
    animation: fly_rocket 3.5s ease-in-out 0s infinite
    z-index: -1
    margin-top: 8vh
    @media (min-width: 768px)
      max-width: 145px
      width: 18vh
      margin-top: 5.5vh
    @media (min-width: 1024px)
      animation: fly_rocket 5.5s ease-in-out 0s infinite
      margin-top: 22.5vh
    @media (min-width: 1441px)
      margin-top: 52vh
      max-width: 100%
      animation: fly_rocket_big 8.5s ease-in-out 0s infinite
  &__image-wrapper
    position: relative
    flex: 1 1 auto
    width: 100%
    height: 100%
    transition: transform 2s
    pointer-events: none
    &[data-position="0"]
      transform: translate(0,0)
    &[data-position="1"]
      transform: translate(0,calc(50vh + 55%))
      @media (min-width: 1024px)
        transform: translate(0,calc(50vh + 50%))
      @media (min-width: 1441px) and (max-height: 1000px)
        transform: translate(0,calc(50vh + 42%)) scale(.85)
    &[data-position="2"]
      transform: translate(0, calc(115vh + 100%))
      @media (min-width: 1024px)
        transform: translate(0,calc(132vh + 100%)) scale(.8)
      @media (min-width: 1441px)
        transform: translate(0,calc(125vh + 100%)) scale(.7)
    .kid
      display: block
      max-width: 216px
      max-height: calc(100% - 30px)
      position: absolute
      top: 17%
      left: 0
      right: 0
      margin: 0 auto
      pointer-events: none
      animation: fly 3s ease 0s infinite normal forwards
      @media (min-width: 768px)
        max-width: 245px
      @media (min-width: 1024px)
        max-width: 295px
      @media (min-width: 1441px)
        max-width: 100%
        max-height: unset
        top: 14%

