.header
  position: absolute
  top: 0
  left: 0
  width: 100%
  background: rgba(255, 255, 255, 0.1)
  backdrop-filter: blur(7px)
  padding: 40px 20px 15px
  z-index: 10
  @media (min-width: 1024px)
    padding: 20px
  &__content
    display: flex
    align-items: center
    justify-content: flex-start
    @media (min-width: 768px)
      max-width: 645px
      margin: 0 auto
    @media (min-width: 1024px)
      max-width: 945px
    @media (min-width: 1441px)
      max-width: 1712px
    .logo-link
      display: block
      width: auto
      .logo-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
        @media (min-width: 1024px)
          width: 130px
        @media (min-width: 1441px)
          width: 145px
    .phone-link
      display: inline-block
      margin: 0 0 0 auto
      color: #fff
      font-family: Lineatura, sans-serif
      font-weight: 700
      font-size: 14px
      line-height: 18px
      position: relative
      top: -3px
      @media (min-width: 1024px)
        font-size: 18px